import React from 'react';
import { useDispatch } from 'react-redux';
import { STYLES_ENUM } from '../../../../models/types';
import { setStyle, incrementModalStep } from '../../../../redux/actions';

const StyleStep = () => {
  const dispatch = useDispatch();

  const selectStyle = (e: any, gender: string) => {
    e.stopPropagation();
    dispatch(setStyle(gender));
    dispatch(incrementModalStep());
  };

  return (
    <div className="step-container animate__animated animate__fadeInRight">
      <div className="step-buttons">

        <button
          className="step-buttons-btn"
          onClick={(e) => selectStyle(e, STYLES_ENUM.denim)}
        >
          домашний
        </button>

        <button
          className="step-buttons-btn"
          onClick={(e) => selectStyle(e, STYLES_ENUM.street)}
        >
          уличный
        </button>

        <button
          className="step-buttons-btn"
          onClick={(e) => selectStyle(e, STYLES_ENUM.office)}
        >
          офисный
        </button>

        <button
          className="step-buttons-btn"
          onClick={(e) => selectStyle(e, STYLES_ENUM.any)}
        >
          любой
        </button>

      </div>
    </div>
  )
};

export default StyleStep;
