import { IProduct, GENDERS_ENUM, STYLES_ENUM, COLORS_ENUM } from "./types";

export const checkGenderFilter = (filters: any, product: IProduct) => {
  if (filters.gender === GENDERS_ENUM.any) {
    return true;
  }
  return filters.gender === product.gender;
};

export const checkStyleFilter = (filters: any, product: IProduct) => {
  if (filters.style === STYLES_ENUM.any) {
    return true;
  }
  return product.styles.some(style => style === filters.style);
};

export const checkColorFilter = (filters: any, product: IProduct) => {
  if (filters.color === COLORS_ENUM.any) {
    return true;
  }
  return filters.color === product.color;
};

export const checkPriceFilter = (filters: any, product: IProduct) => {
  const productPrice = Number(product.price);
  return productPrice >= filters.priceRange[0] && productPrice <= filters.priceRange[1];
};
