import React from 'react';
import { useDispatch } from 'react-redux';
import { COLORS_ENUM } from '../../../../models/types';
import { setColor } from '../../../../redux/actions';
import { useHistory } from 'react-router-dom';

const ToneStep = () => {
  const dispatch = useDispatch();
  const history = useHistory();

  const selectTone = (e: any, gender: string) => {
    e.stopPropagation();
    dispatch(setColor(gender));
    history.push('/catalog');
  };

  return (
    <div className="step-container animate__animated animate__fadeInUp">
      <div className="step-buttons">

        <button
          className="step-buttons-btn"
          onClick={(e) => selectTone(e, COLORS_ENUM.light)}
        >
          Светлый
        </button>

        <button
          className="step-buttons-btn"
          onClick={(e) => selectTone(e, COLORS_ENUM.dark)}
        >
          Темный
        </button>

        <button
          className="step-buttons-btn"
          onClick={(e) => selectTone(e, COLORS_ENUM.any)}
        >
          Любой
        </button>

      </div>
    </div>
  )
};

export default ToneStep;
