import React from 'react';
import { useDispatch } from 'react-redux';
import { setGender, incrementModalStep } from '../../../../redux/actions';
import { GENDERS_ENUM } from '../../../../models/types';

const GenderStep = () => {
  const dispatch = useDispatch();

  const selectGender = (e: any, gender: string) => {
    e.stopPropagation();
    dispatch(setGender(gender));
    dispatch(incrementModalStep());
  };

  return (
    <div className="step-container animate__animated animate__fadeInLeft">
      <div className="step-buttons">

        <button
          className="step-buttons-btn"
          onClick={(e) => selectGender(e, GENDERS_ENUM.male)}
        >
          Мужское
        </button>

        <button
          className="step-buttons-btn"
          onClick={(e) => selectGender(e, GENDERS_ENUM.female)}
        >
          Женское
        </button>
        
      </div>
    </div>
  )
};

export default GenderStep;
