import actionTypes from './actionTypes';
import { GENDERS_ENUM, STYLES_ENUM, COLORS_ENUM, MODAL_STEP_ENUM } from '../models/types';
import Mock from './mock';

export type Action = {
  type: string;
  payload?: any;
};

const initialState = {
  filters: {
    gender: GENDERS_ENUM.any,
    style: STYLES_ENUM.any,
    color: COLORS_ENUM.any,
    priceRange: [2000, 40000]
  },
  products: Mock.products,
  modalStep: MODAL_STEP_ENUM.genderStep
}

export default function(state = initialState, action: Action) {
  switch (action.type) {
    case actionTypes.SET_GENDER:
      return {
        ...state,
        filters: {
          ...state.filters,
          gender: action.payload
        }
      };
    case actionTypes.SET_STYLE:
      return {
        ...state,
        filters: {
          ...state.filters,
          style: action.payload
        }
      };
    case actionTypes.SET_COLOR:
      return {
        ...state,
        filters: {
          ...state.filters,
          color: action.payload
        }
      };
    case actionTypes.SET_PRICE_RANGE:
      return {
        ...state,
        filters: {
          ...state.filters,
          priceRange: action.payload
        }
      }
    case actionTypes.INCREMENT_MODAL_STEP:
      return {
        ...state,
        modalStep: state.modalStep + 1
      }
    case actionTypes.DECREMENT_MODAL_STEP:
      return {
        ...state,
        modalStep: state.modalStep - 1
      }
    case actionTypes.SET_DEFAULT_MODAL_STEP:
      return {
        ...state,
        modalStep: MODAL_STEP_ENUM.genderStep
      }
    default:
      return state;
  }
}
