import React from 'react';
import { Icon, Collapse, RadioGroup, Radio, RangeSlider, Intent } from '@blueprintjs/core';
import { IconNames } from '@blueprintjs/icons';
import { GENDERS_ENUM, STYLES_ENUM, COLORS_ENUM } from '../../../../models/types';

type Props = {
  isFiltersVisible: boolean;
  filters: {
    gender: string;
    style: string;
    color: string;
    priceRange: any;
  };
  toggleFiltersVisible: () => void;
  handleGenderFilters: (gender: string | any) => void;
  handleStyleFilters: (style: string | any) => void;
  handleColorFilters: (color: string | any) => void;
  handlePriceRangeFilter: (range: Array<number> | any) => void;
}

const Filter = ({
  isFiltersVisible,
  filters,
  toggleFiltersVisible,
  handleGenderFilters,
  handleStyleFilters,
  handleColorFilters,
  handlePriceRangeFilter
}: Props) => {
    
  return (
    <div className="catalog-filters">

      <div
        className="catalog-filters-header"
        onClick={ toggleFiltersVisible }
      >
        { isFiltersVisible ? <span>Скрыть фильтры</span> : <span>Показать фильтры</span> }
        <Icon
          color="#fff"
          icon={ isFiltersVisible ? IconNames.CROSS : IconNames.FILTER }
        />
      </div>

      <Collapse
        isOpen={ isFiltersVisible }
        className={ isFiltersVisible ? 'catalog-filters-filter open' : 'catalog-filters-filter' }
      >

        <div className="catalog-filters-filter-block">
          <RadioGroup
            label="пол"
            onChange={ handleGenderFilters }
            selectedValue={ filters.gender }
          >
            <Radio
              label="мужской"
              className="bp3-large"
              value={ GENDERS_ENUM.male }
            />
            <Radio
              label="женский"
              className="bp3-large"
              value={ GENDERS_ENUM.female }
            />
            <Radio
              label="любой"
              className="bp3-large"
              value={ GENDERS_ENUM.any }
            />
          </RadioGroup>
        </div>

        <div className="catalog-filters-filter-block">
          <RadioGroup
            label="стиль"
            onChange={ handleStyleFilters }
            selectedValue={ filters.style }
          >
            <Radio
              label="домашний"
              className="bp3-large"
              value={ STYLES_ENUM.denim }
            />
            <Radio
              label="уличный"
              className="bp3-large"
              value={ STYLES_ENUM.street }
            />
            <Radio
              label="офисный"
              className="bp3-large"
              value={ STYLES_ENUM.office }
            />
            <Radio
              label="любой"
              className="bp3-large"
              value={ STYLES_ENUM.any }
            />
          </RadioGroup>
        </div>

        <div className="catalog-filters-filter-block">
          <RadioGroup
            label="тон"
            onChange={ handleColorFilters }
            selectedValue={ filters.color }
          >
            <Radio
              label="темный"
              className="bp3-large"
              value={ COLORS_ENUM.dark }
            />
            <Radio
              label="светлый"
              className="bp3-large"
              value={ COLORS_ENUM.light }
            />
            <Radio
              label="любой"
              className="bp3-large"
              value={ COLORS_ENUM.any }
            />
          </RadioGroup>
        </div>

        <div className="catalog-filters-filter-block">
          <label className="bp3-label">цена</label>
          <RangeSlider
            intent={ Intent.PRIMARY }
            onChange={ handlePriceRangeFilter }
            value={ filters.priceRange }
            stepSize={ 500 }
            min={ 2000 }
            max={ 40000 }
            labelStepSize={ 40000 }
            labelRenderer={(num) => {
              return <span>{num}₽</span>
            }}
          />
        </div>

      </Collapse>

    </div>
  )
}

export default Filter;
