import React from 'react';
import './Modal.scss';
import { MODAL_STEP_ENUM } from '../../../models/types';
import { connect } from 'react-redux';
import GenderStep from './steps/GenderStep';
import StyleStep from './steps/StyleStep';
import ToneStep from './steps/ToneStep';

type Props = {
  closeModal: () => void,
  modalStep?: number
}

const mapStateToProps = (state: any) => {
  const { modalStep } = state;
  return {
    modalStep
  };
};

const Modal = ({ closeModal, modalStep }: Props) => {
  return (
    <div className="modal-wrapper animate__animated animate__fadeIn" onClick={closeModal}>
      <div className="modal-body">
        { modalStep === MODAL_STEP_ENUM.genderStep && (
            <GenderStep />
          )
        }
        { modalStep === MODAL_STEP_ENUM.styleStep && (
            <StyleStep />
          )
        }
        { modalStep === MODAL_STEP_ENUM.toneStep && (
            <ToneStep />
          )
        }
      </div>
    </div>
  )
};

export default connect(mapStateToProps)(Modal);;
