import React from 'react';
import './Home.scss';
import Logo from '../../../assets/main/logo.svg';
import Modal from '../../core/modal/Modal';
import { useDispatch } from 'react-redux';
import { setDefaultModalStep } from '../../../redux/actions';

const Home = () => {

  const dispatch = useDispatch();

  const [isVisibleModal, setIsVisibleModal] = React.useState(false);

  const openModal = () => {
    setIsVisibleModal(true);
  }

  const closeModal = () => {
    setIsVisibleModal(false);
    dispatch(setDefaultModalStep());
  }
  
  React.useEffect(() => {
    document.title = "E A S Y";
    dispatch(setDefaultModalStep());
  }, [dispatch]);

  return (
    <div className="home">

      {isVisibleModal ? <Modal closeModal={closeModal} /> : (
          <div className="container">

            <div className="home-center animate__animated animate__fadeIn">
              <div className="home-center-logo animate__animated animate__fadeIn">
                <img src={ Logo } alt="" />
              </div>
              <div className="home-center-btn animate__animated animate__fadeInLeft">
                <button type="button" onClick={openModal}>начать</button>
              </div>
              <div className="home-center-description animate__animated animate__fadeInRight">
                easy - меняться, легко!
              </div>
            </div>

          </div>
        )
      }


    </div>
  )
}

export default Home;
