import actionTypes from "./actionTypes";
import { Action } from "./reducer";

export type ISetGender = (gender: string) => Action;
export const setGender: ISetGender = (gender) => ({
  type: actionTypes.SET_GENDER,
  payload: gender
});

export type ISetStyle = (style: string) => Action;
export const setStyle: ISetStyle = (style) => ({
  type: actionTypes.SET_STYLE,
  payload: style
});

export type ISetColor = (color: string) => Action;
export const setColor: ISetColor = (color) => ({
  type: actionTypes.SET_COLOR,
  payload: color
});

export type ISetPriceRange = (range: Array<number>) => Action;
export const setPriceRange: ISetPriceRange = (range) => ({
  type: actionTypes.SET_PRICE_RANGE,
  payload: range
});

export type IIncrementModalStep = () => Action;
export const incrementModalStep: IIncrementModalStep = () => ({
  type: actionTypes.INCREMENT_MODAL_STEP
});

export type IDecrementModalStep = () => Action;
export const decrementModalStep: IDecrementModalStep = () => ({
  type: actionTypes.DECREMENT_MODAL_STEP
});

export type ISetDefaultModalStep = () => Action;
export const setDefaultModalStep: ISetDefaultModalStep = () => ({
  type: actionTypes.SET_DEFAULT_MODAL_STEP
});