import React from 'react';
import './Catalog.scss';
import { connect, useDispatch } from 'react-redux';
import { setGender, setStyle, setColor, setPriceRange } from '../../../redux/actions';
import Filters from './filters/Filters';
import ProductsCatalog from '../../core/products-catalog/ProductsCatalog';
import { IProduct } from '../../../models/types';
import { checkGenderFilter, checkStyleFilter, checkColorFilter, checkPriceFilter } from '../../../models/validationFilters';

const mapStateToProps = (state: any) => {
  const { filters, products } = state;
  return {
    filters,
    products
  };
};

const Catalog = ({ filters, products }: any) => {

  const filteredProducts = React.useMemo(() => {
    return products.filter((product: IProduct) => {
      return checkGenderFilter(filters, product) &&
              checkStyleFilter(filters, product) &&
              checkColorFilter(filters, product) &&
              checkPriceFilter(filters, product);
    });
  }, [filters, products]);

  const dispatch = useDispatch();

  const [isFiltersVisible, setIsFiltersVisible] = React.useState(true);

  const toggleFiltersVisible = React.useCallback(() => {
    setIsFiltersVisible(!isFiltersVisible);
  }, [isFiltersVisible]);

  const handleGenderFilters = (e: any) => {
    dispatch(setGender(e.target.value));
  }

  const handleStyleFilters = (e: any) => {
    dispatch(setStyle(e.target.value));
  }

  const handleColorFilters = (e: any) => {
    dispatch(setColor(e.target.value));
  }

  const handlePriceRangeFilter = (range: Array<number>) => {
    dispatch(setPriceRange(range));
  }

  React.useEffect(() => {
    document.title = "E A S Y - Каталог";
  }, []);

  return (
    <div className="catalog">
      <div className="container">
        <div
          className={ isFiltersVisible ? "catalog-wrapper animate__animated animate__fadeIn" : "catalog-wrapper content-column animate__animated animate__fadeIn" }
        >
          <Filters
            isFiltersVisible={ isFiltersVisible }
            filters={ filters }
            toggleFiltersVisible={ toggleFiltersVisible }
            handleGenderFilters={ handleGenderFilters }
            handleStyleFilters={ handleStyleFilters }
            handleColorFilters={ handleColorFilters }
            handlePriceRangeFilter={ handlePriceRangeFilter }
          />

          <ProductsCatalog
            products={ filteredProducts }
          />
          
        </div>
      </div>
    </div>
  )
};

export default connect(mapStateToProps)(Catalog);
